import React from 'react';
import { Route, Switch } from 'react-router-dom';

import routes from './data';

const Routes = () => {
  return (
    <Switch>
      {routes.map((route, idx) => (
        <Route
          key={`${route.path}_${idx}`}
          path={route.path}
          exact={route.exact}
          component={route.component}
        />
      ))}
    </Switch>
  );
};

export default Routes;
